import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function assetTurnoverTtm(tableName: string) {
  const title = "Asset Turnover (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["assetTurnoverTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Sales per dollar of total assets.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about asset turnover or other relevant asset information?`,
              value: info.row.original.financialData.companyRatios?.data?.assetTurnoverTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.assetTurnoverTtm}
        />
      )
    },
  })
}

export { assetTurnoverTtm }
